// src/renderUtils.ts
// Rendering utilities + gauge painters (arc + bar)
// -----------------------------------------------------------------------------
// Public API consumed by exportUtils.ts:
//   - clamp, pad, buildCanvas
//   - drawArcGauge(ctx, preset, value01)
//   - drawBarGauge(ctx, preset, value01)
//   - runSelfTests()
//
// Notes:
// - This file is intentionally tolerant about the exact shape of `preset`
//   to avoid breaking your existing types. Optional fields are guarded.
// - Glow color follows the main stroke paint (solid/gradient) automatically.
// - Circular segmented mode renders WHOLE segments only (no partial wedges).
// - Warning zones overlay the main stroke; they never hide it.
// - Glow modes are mutually-exclusive at render time: "soft" | "ring" | "legacy".
//   (We keep drawGlowStroke() as a helper for future flexibility.)
// -----------------------------------------------------------------------------

// ---------- tiny helpers -----------------------------------------------------
export const clamp = (v: number, min: number, max: number) =>
  Math.max(min, Math.min(max, v));

export const pad = (n: number, width: number) => String(n).padStart(width, "0");

const dpr = () =>
  typeof window !== "undefined" ? window.devicePixelRatio || 1 : 1;

export function buildCanvas(
  ctx: CanvasRenderingContext2D,
  width: number,
  height: number
) {
  const ratio = dpr();
  const w = Math.max(1, Math.floor(width * ratio));
  const h = Math.max(1, Math.floor(height * ratio));
  ctx.canvas.width = w;
  ctx.canvas.height = h;
  ctx.canvas.style.width = `${width}px`;
  ctx.canvas.style.height = `${height}px`;
  ctx.setTransform(ratio, 0, 0, ratio, 0, 0);
  ctx.imageSmoothingEnabled = true;
  // @ts-ignore (not in TS lib typings in some targets)
  ctx.imageSmoothingQuality = "high";
}

function rgbaToParts(color: string) {
  if (!color) return { r: 0, g: 0, b: 0, a: 1 };
  if (color.startsWith("#")) {
    const c = color.slice(1);
    const hex = c.length === 3 ? c.split("").map(x => x + x).join("") : c.padEnd(6, "0");
    const n = parseInt(hex, 16);
    return { r: (n >> 16) & 255, g: (n >> 8) & 255, b: n & 255, a: 1 };
  }
  const m = color.match(/rgba?\(([^)]+)\)/i);
  if (m) {
    const p = m[1].split(",").map(s => parseFloat(s));
    return { r: p[0] || 0, g: p[1] || 0, b: p[2] || 0, a: p[3] ?? 1 };
  }
  return { r: 0, g: 0, b: 0, a: 1 };
}

function withOpacity(color: string, mul: number) {
  const c = rgbaToParts(color);
  const a = clamp((c.a ?? 1) * mul, 0, 1);
  return `rgba(${c.r},${c.g},${c.b},${a})`;
}

// ---------- geometry & paints -----------------------------------------------
export function arcAnglesFromOpening(direction: string) {
  // Opening direction = CENTER of the 90 gap.
  const dirDeg =
    ({ right: 0, top: 90, left: 180, bottom: 270 } as Record<string, number>)[
      direction
    ] ?? 270;
  const startDeg = dirDeg + 45; // arc starts just after the gap
  const sweepDeg = 270;
  return {
    startAngle: (startDeg * Math.PI) / 180,
    sweep: (sweepDeg * Math.PI) / 180,
  };
}

function roundRect(
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  w: number,
  h: number,
  r: number
) {
  const rr = clamp(r, 0, Math.min(w, h) / 2);
  ctx.beginPath();
  ctx.moveTo(x + rr, y);
  ctx.lineTo(x + w - rr, y);
  ctx.quadraticCurveTo(x + w, y, x + w, y + rr);
  ctx.lineTo(x + w, y + h - rr);
  ctx.quadraticCurveTo(x + w, y + h, x + w - rr, y + h);
  ctx.lineTo(x + rr, y + h);
  ctx.quadraticCurveTo(x, y + h, x, y + h - rr);
  ctx.lineTo(x, y + rr);
  ctx.quadraticCurveTo(x, y, x + rr, y);
  ctx.closePath();
}

function makeCanvasLinear(
  ctx: CanvasRenderingContext2D,
  rect: { x: number; y: number; w: number; h: number },
  stops: Array<{ pos: number; color: string }>
) {
  const g = ctx.createLinearGradient(rect.x, rect.y, rect.x + rect.w, rect.y + rect.h);
  stops.forEach(s => g.addColorStop(clamp(s.pos, 0, 1), s.color));
  return g;
}

function mainPaint(
  ctx: CanvasRenderingContext2D,
  rect: { x: number; y: number; w: number; h: number },
  main: any
) {
  const stops =
    main?.fillMode === "solid"
      ? [
          { pos: 0, color: main?.colorSolid ?? "#00C2FF" },
          { pos: 1, color: main?.colorSolid ?? "#00C2FF" },
        ]
      : main?.gradient?.stops ?? [{ pos: 0, color: "#00E0FF" }, { pos: 1, color: "#FFD400" }];
  return makeCanvasLinear(ctx, rect, stops);
}

function warnPaint(
  ctx: CanvasRenderingContext2D,
  rect: { x: number; y: number; w: number; h: number },
  warn: any
) {
  const stops =
    warn?.mode === "solid"
      ? [
          { pos: 0, color: warn?.colorSolid ?? "#FFA500" },
          { pos: 1, color: warn?.colorSolid ?? "#FFA500" },
        ]
      : warn?.gradient?.stops ?? [{ pos: 0, color: "#FFA500" }, { pos: 1, color: "#FF3B30" }];
  return makeCanvasLinear(ctx, rect, stops);
}

// ---------- Glow helpers (mutually-exclusive at call site) -------------------
// We support 3 visual families:
//
//   1) SOFT HALO  (blurred, fades inward & outward)
//   2) RING GLOW  (crisp ring along stroke; can use multiple passes)
//   3) LEGACY     (double rings: inner & outer, classic double frame look)
//
// All glows use additive blending (lighter) and their color follows the main
// paint (solid/gradient). Strength/thickness are clamped safely.

function drawSoftHalo(
  ctx: CanvasRenderingContext2D,
  path: () => void,
  lineWidth: number,
  paint: string | CanvasGradient,
  opts: {
    inner: boolean;
    outer: boolean;
    thickness: number; // 0..20
    strength: number; // 0..40 typical
  }
) {
  if ((!opts.inner && !opts.outer) || opts.thickness <= 0 || opts.strength <= 0) return;

  const passes = Math.max(2, Math.min(16, Math.round(opts.thickness * 1.4)));
  const baseAlpha = clamp(opts.strength / 80, 0.05, 0.75);

  ctx.save();
  ctx.globalCompositeOperation = "lighter";
  ctx.strokeStyle = paint as any;

  for (let i = 1; i <= passes; i++) {
    const t = i / passes;
    // Outward aura grows wider; inward variant narrows the width.
    const widen = lineWidth * (1 + t * (opts.thickness / 3.2)); // outer
    const innerNarrow = lineWidth * Math.max(0.1, 1 - t * (opts.thickness / 10)); // inner
    ctx.globalAlpha = baseAlpha * (1 - t * 0.9);

    if (opts.outer) {
      ctx.lineWidth = widen;
      ctx.beginPath();
      path();
      ctx.stroke();
    }
    if (opts.inner) {
      ctx.lineWidth = innerNarrow;
      ctx.beginPath();
      path();
      ctx.stroke();
    }
  }

  ctx.restore();
}

function drawRingGlow(
  ctx: CanvasRenderingContext2D,
  path: () => void,
  lineWidth: number,
  paint: string | CanvasGradient,
  passes: number,
  strength: number
) {
  if (passes <= 0 || strength <= 0) return;

  ctx.save();
  ctx.globalCompositeOperation = "lighter";
  ctx.strokeStyle = paint as any;

  const baseAlpha = clamp(strength / 60, 0.05, 0.65);
  for (let i = 0; i < passes; i++) {
    const t = i / Math.max(1, passes - 1);
    ctx.globalAlpha = baseAlpha * (1 - t * 0.55);
    ctx.lineWidth = lineWidth * (1 + 0.35 * t);
    ctx.beginPath();
    path();
    ctx.stroke();
  }
  ctx.restore();
}

function drawLegacyDouble(
  ctx: CanvasRenderingContext2D,
  path: () => void,
  lineWidth: number,
  paint: string | CanvasGradient,
  outerThickness: number // 0..20
) {
  if (outerThickness <= 0) return;

  ctx.save();
  ctx.globalCompositeOperation = "lighter";
  ctx.strokeStyle = paint as any;

  // **Legacy look** (matches deployed behavior closely):
  // - Inner ring: slightly thinner than main, subtle alpha.
  ctx.globalAlpha = 0.25;
  ctx.lineWidth = lineWidth * 0.8;
  ctx.beginPath();
  path();
  ctx.stroke();

  // - Outer ring: scaled by user; a bit stronger alpha.
  ctx.globalAlpha = 0.35;
  ctx.lineWidth = lineWidth * (1 + outerThickness / 10);
  ctx.beginPath();
  path();
  ctx.stroke();

  ctx.restore();
}

/**
 * Helper retained for compatibility & future use.
 * Not called by the new mutually-exclusive glow path (we call the specific
 * functions above instead), but you asked to keep it available.
 */
export function drawGlowStroke(
  ctx: CanvasRenderingContext2D,
  pathFn: () => void,
  baseThickness: number,
  color: string,
  strength: number,
  thickness: number,
  mode: "soft" | "ring" | "legacy" = "soft"
) {
  ctx.save();
  ctx.lineJoin = "round";
  ctx.lineCap = "round";

  if (mode === "soft") {
    ctx.shadowColor = color;
    ctx.shadowBlur = strength * 2.5;
    ctx.globalAlpha = 0.9;
    ctx.lineWidth = baseThickness * thickness;
    ctx.strokeStyle = color;
    pathFn();
    ctx.stroke();
  } else if (mode === "ring") {
    ctx.globalAlpha = 0.8;
    ctx.lineWidth = baseThickness * thickness;
    ctx.strokeStyle = color;
    pathFn();
    ctx.stroke();
  } else {
    // legacy
    ctx.globalAlpha = 0.25;
    ctx.lineWidth = baseThickness * 0.8;
    ctx.strokeStyle = color;
    pathFn();
    ctx.stroke();

    ctx.globalAlpha = 0.35;
    ctx.lineWidth = baseThickness * (1 + thickness / 10);
    ctx.strokeStyle = color;
    pathFn();
    ctx.stroke();
  }

  ctx.restore();
}

// Interpret a variety of previous glow preset shapes into a single struct
function readGlow(preset: any) {
  const g = preset?.glow ?? {};
  // Newer shape may have g.mode; older had separate booleans.
  let mode: "soft" | "ring" | "legacy" =
    g.mode && (g.mode === "soft" || g.mode === "ring" || g.mode === "legacy")
      ? g.mode
      : g.legacyDouble
      ? "legacy"
      : g.ringEnabled
      ? "ring"
      : "soft";

  return {
    enabled: !!g.enabled,
    mode,
    // soft halo
    haloInner: !!g.haloInner, // inward fade
    haloOuter: g.haloOuter !== false, // outward fade; default true
    haloThickness: clamp(g.haloThickness ?? g.thickness ?? 1.25, 0, 20),
    // ring
    ringPasses: clamp(g.ringPasses ?? 2, 0, 10),
    // legacy double
    legacyOuterThickness: clamp(g.legacyOuterThickness ?? 2, 0, 20),
    // shared
    strength: clamp(g.strength ?? 12, 0, 60),
  };
}

// ---------- ARC gauge --------------------------------------------------------
export function drawArcGauge(
  ctx: CanvasRenderingContext2D,
  preset: any,
  value01: number
) {
  const { canvas, arc, base, main, warnings, openingDirection } = preset;
  const cx = (canvas?.width ?? 512) / 2;
  const cy = (canvas?.height ?? 512) / 2;
  const radius = arc?.radius ?? 200;
  const thick = arc?.thickness ?? 24;
  const round = !!arc?.roundCaps;
  const { startAngle, sweep } = arcAnglesFromOpening(openingDirection ?? "bottom");

  ctx.lineCap = round ? "round" : "butt";

  const progress = clamp(value01, 0, 1);
  const rect = { x: cx - radius, y: cy - radius, w: radius * 2, h: radius * 2 };
  const mainStyle = mainPaint(ctx, rect, main);

  // Base stroke  full arc, underneath everything else
  if (base?.enabled) {
    const baseStyle = withOpacity(base.color ?? "rgba(50,50,50,1)", base.opacity ?? 0.6);
    const baseW = (base.sameGeometryAsMain ? (base.thicknessScale ?? 1) : 1) * thick;
    ctx.strokeStyle = baseStyle;
    ctx.lineWidth = baseW;
    ctx.beginPath();
    ctx.arc(cx, cy, radius, startAngle, startAngle + sweep);
    ctx.stroke();
  }

  // Continuous vs segmented
  const segmented = !!main?.segmented;

  if (!segmented) {
    // ---- Continuous main
    const endAngle = startAngle + sweep * progress;
    const pathMain = () => ctx.arc(cx, cy, radius, startAngle, endAngle);

    // main
    ctx.strokeStyle = mainStyle as any;
    ctx.lineWidth = thick;
    ctx.beginPath();
    pathMain();
    ctx.stroke();

    // frame (optional)
    if (main?.frame?.enabled) {
      ctx.strokeStyle = main.frame.color ?? "rgba(255,255,255,0.9)";
      ctx.lineWidth = thick * (main.frame.thickness ?? 1.25);
      ctx.beginPath();
      pathMain();
      ctx.stroke();
    }

    // overlays (do NOT erase main)
    const sLen = clamp(warnings?.start?.lengthPct ?? 0, 0, 0.5);
    const eLen = clamp(warnings?.end?.lengthPct ?? 0, 0, 0.5);

    if (warnings?.start?.enabled && progress <= sLen) {
      const style = warnPaint(ctx, rect, warnings.start);
      ctx.strokeStyle = style as any;
      ctx.lineWidth = thick;
      ctx.beginPath();
      ctx.arc(cx, cy, radius, startAngle, endAngle);
      ctx.stroke();
    }

    if (warnings?.end?.enabled && progress >= 1 - eLen) {
      const zoneStart = startAngle + sweep * (1 - eLen);
      const from = Math.max(zoneStart, startAngle);
      if (endAngle > from) {
        const style = warnPaint(ctx, rect, warnings.end);
        ctx.strokeStyle = style as any;
        ctx.lineWidth = thick;
        ctx.beginPath();
        ctx.arc(cx, cy, radius, from, endAngle);
        ctx.stroke();
      }
    }

    // glow (mutually-exclusive at call)
    const glow = readGlow(preset);
    if (glow.enabled) {
      const path = pathMain;
      if (glow.mode === "soft") {
        drawSoftHalo(ctx, path, thick, mainStyle as any, {
          inner: glow.haloInner,
          outer: glow.haloOuter,
          thickness: glow.haloThickness,
          strength: glow.strength,
        });
      } else if (glow.mode === "ring") {
        drawRingGlow(ctx, path, thick, mainStyle as any, glow.ringPasses, glow.strength);
      } else {
        drawLegacyDouble(ctx, path, thick, mainStyle as any, glow.legacyOuterThickness);
      }
    }

    return;
  }

  // ---- Segmented (WHOLE segments only)
  const segCount = Math.max(1, main?.segments | 0);
  const gap = clamp(main?.segmentGap ?? 0, 0, thick * 0.8);
  const angGap = gap / radius; // radians
  const segSweep = (sweep - (segCount - 1) * angGap) / segCount;

  const filledSegments = Math.floor(progress * segCount);

  // warning windows
  const sLen = clamp(warnings?.start?.lengthPct ?? 0, 0, 0.5);
  const eLen = clamp(warnings?.end?.lengthPct ?? 0, 0, 0.5);
  const showStartWarn = warnings?.start?.enabled && progress <= sLen;
  const showEndWarn = warnings?.end?.enabled && progress >= 1 - eLen;
  const endWarnStartAngle = startAngle + sweep * (1 - eLen);

  for (let i = 0; i < filledSegments; i++) {
    const segStart = startAngle + i * (segSweep + angGap);
    const segEnd = segStart + segSweep;

    // choose paint (warning zones overlay)
    let paint: any = mainStyle;
    if (showStartWarn && segEnd <= startAngle + sweep * sLen) {
      paint = warnPaint(ctx, rect, warnings.start);
    } else if (showEndWarn && segStart >= endWarnStartAngle) {
      paint = warnPaint(ctx, rect, warnings.end);
    }

    const pathSeg = () => ctx.arc(cx, cy, radius, segStart, segEnd);

    // draw
    ctx.strokeStyle = paint as any;
    ctx.lineWidth = thick;
    ctx.beginPath();
    pathSeg();
    ctx.stroke();

    // frame
    if (main?.frame?.enabled) {
      ctx.strokeStyle = main.frame.color ?? "rgba(255,255,255,0.9)";
      ctx.lineWidth = thick * (main.frame.thickness ?? 1.25);
      ctx.beginPath();
      pathSeg();
      ctx.stroke();
    }

    // segment glow
    const glow = readGlow(preset);
    if (glow.enabled) {
      if (glow.mode === "soft") {
        drawSoftHalo(ctx, pathSeg, thick, paint as any, {
          inner: glow.haloInner,
          outer: glow.haloOuter,
          thickness: glow.haloThickness,
          strength: glow.strength,
        });
      } else if (glow.mode === "ring") {
        drawRingGlow(ctx, pathSeg, thick, paint as any, glow.ringPasses, glow.strength);
      } else {
        drawLegacyDouble(ctx, pathSeg, thick, paint as any, glow.legacyOuterThickness);
      }
    }
  }
}

// ---------- BAR gauge --------------------------------------------------------
export function drawBarGauge(
  ctx: CanvasRenderingContext2D,
  preset: any,
  value01: number
) {
  const { canvas, bar, base, main, warnings } = preset;
  const cw = canvas?.width ?? 512;
  const ch = canvas?.height ?? 512;

  const len = bar?.length ?? 420;
  const thick = bar?.thickness ?? 24;
  const horiz = (bar?.orientation ?? "horizontal") === "horizontal";
  const dir = bar?.direction ?? (horiz ? "ltr" : "ttb"); // ltr|rtl|ttb|btt
  const r = clamp(bar?.squareEnds ? 0 : bar?.cornerRadius ?? 12, 0, thick / 2);

  const cx = cw / 2;
  const cy = ch / 2;
  const x0 = cx - (horiz ? len / 2 : thick / 2);
  const y0 = cy - (horiz ? thick / 2 : len / 2);
  const w = horiz ? len : thick;
  const h = horiz ? thick : len;

  const progress = clamp(value01, 0, 1);

  const rectPaint = { x: x0, y: y0, w, h };
  const mainStyle = mainPaint(ctx, rectPaint, main);

  // Base track
  if (base?.enabled) {
    const strokeColor = withOpacity(base.color ?? "rgba(50,50,50,1)", base.opacity ?? 0.6);

    if (base.sameGeometryAsMain) {
      // Filled rounded track scaled by thicknessScale
      const minor = horiz ? h : w;
      const scaledMinor = minor * (base.thicknessScale ?? 1);
      const delta = scaledMinor - minor;
      const nx0 = horiz ? x0 : x0 - delta / 2;
      const ny0 = horiz ? y0 - delta / 2 : y0;
      const nw = horiz ? w : scaledMinor;
      const nh = horiz ? scaledMinor : h;
      ctx.save();
      ctx.fillStyle = strokeColor;
      roundRect(ctx, nx0, ny0, nw, nh, r);
      ctx.fill();
      ctx.restore();
    } else {
      // Outline-style base (approximate)
      ctx.save();
      ctx.strokeStyle = strokeColor;
      ctx.lineWidth = h;
      roundRect(ctx, x0, y0, w, h, r);
      ctx.stroke();
      ctx.restore();
    }
  }

  // helpers
  const gap = main?.segmented ? clamp(main.segmentGap ?? 0, 0, thick * 0.8) : 0;
  const segCount = main?.segmented ? Math.max(1, main.segments | 0) : 1;
  const axisLen = horiz ? w : h;
  const segLen = (axisLen - (main?.segmented ? (segCount - 1) * gap : 0)) / segCount;

  const startZonePx = warnings?.start?.enabled
    ? axisLen * clamp(warnings.start.lengthPct ?? 0, 0, 0.5)
    : 0;
  const endZonePx = warnings?.end?.enabled
    ? axisLen * clamp(warnings.end.lengthPct ?? 0, 0, 0.5)
    : 0;

  const inStartWindow = warnings?.start?.enabled && progress <= (warnings.start.lengthPct ?? 0);
  const inEndWindow = warnings?.end?.enabled && progress >= 1 - (warnings.end.lengthPct ?? 0);

  function placeRect(offset: number, length: number) {
    if (horiz) {
      if (dir === "rtl") return { x: x0 + w - (offset + length), y: y0, w: length, h };
      return { x: x0 + offset, y: y0, w: length, h };
    } else {
      if (dir === "btt") return { x: x0, y: y0 + h - (offset + length), w, h: length };
      return { x: x0, y: y0 + offset, w, h: length };
    }
  }

  // painter for a single filled block + optional frame + glow
  const drawFillRect = (
    rect: { x: number; y: number; w: number; h: number },
    paint: string | CanvasGradient
  ) => {
    ctx.save();

    // main fill
    ctx.fillStyle = paint as any;
    roundRect(ctx, rect.x, rect.y, rect.w, rect.h, r);
    ctx.fill();

    // frame
    if (main?.frame?.enabled) {
      ctx.strokeStyle = main.frame.color ?? "rgba(255,255,255,0.9)";
      ctx.lineWidth = thick * (main.frame.thickness ?? 1.25);
      roundRect(ctx, rect.x, rect.y, rect.w, rect.h, r);
      ctx.stroke();
    }

    // glow (mutually-exclusive at call)
    const glow = readGlow(preset);
    if (glow.enabled) {
      const paintUse = paint as any;
      const pathFn = () => roundRect(ctx, rect.x, rect.y, rect.w, rect.h, r);

      if (glow.mode === "soft") {
        drawSoftHalo(ctx, pathFn, thick, paintUse, {
          inner: glow.haloInner,
          outer: glow.haloOuter,
          thickness: glow.haloThickness,
          strength: glow.strength,
        });
      } else if (glow.mode === "ring") {
        drawRingGlow(ctx, pathFn, thick, paintUse, glow.ringPasses, glow.strength);
      } else {
        drawLegacyDouble(ctx, pathFn, thick, paintUse, glow.legacyOuterThickness);
      }
    }

    ctx.restore();
  };

  // Continuous
  if (!main?.segmented) {
    const filledLen = axisLen * progress;
    const rectMain = placeRect(0, filledLen);
    drawFillRect(rectMain, mainStyle);

    // overlays
    if (inStartWindow) {
      const wl = Math.min(filledLen, startZonePx);
      if (wl > 0) drawFillRect(placeRect(0, wl), warnPaint(ctx, rectPaint, warnings.start));
    }
    if (inEndWindow) {
      const zoneStart = axisLen - endZonePx;
      const overlap = Math.max(0, filledLen - zoneStart);
      if (overlap > 0)
        drawFillRect(placeRect(zoneStart, overlap), warnPaint(ctx, rectPaint, warnings.end));
    }
    return;
  }

  // Segmented (bars: keep partial fill on last segment  useful visually)
  const filled = progress * segCount;
  for (let i = 0; i < segCount; i++) {
    const off = i * (segLen + gap);
    const frac =
      i < Math.floor(filled) ? 1 : i === Math.floor(filled) ? filled - Math.floor(filled) : 0;
    if (frac <= 0) continue;
    const lenNow = segLen * frac;

    let paint: any = mainStyle;
    if (inStartWindow && off + lenNow <= startZonePx) {
      paint = warnPaint(ctx, rectPaint, warnings.start);
    } else if (inEndWindow && axisLen - off <= endZonePx) {
      paint = warnPaint(ctx, rectPaint, warnings.end);
    }

    drawFillRect(placeRect(off, lenNow), paint);
  }
}

// ---- Lightweight self-tests (safe to remove later) -------------------------
export function runSelfTests() {
  try {
    console.assert(pad(9, 2) === "09" && pad(100, 3) === "100", "pad()");
    const { startAngle } = arcAnglesFromOpening("bottom");
    console.assert(Math.abs(startAngle - (315 * Math.PI) / 180) < 1e-6, "opening center mapping");
    // eslint-disable-next-line no-console
    console.info("[renderUtils] self-tests passed");
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error("[renderUtils] self-tests failed", e);
  }
}
