// src/exportUtils.ts
// Handles PNG export with proper state handling for segmented and continuous modes.

import JSZip from "jszip";
import type { Preset } from "./types";
import { buildCanvas, clamp, pad, drawArcGauge, drawBarGauge } from "./renderUtils";

/**
 * Decide how many PNG frames to export based on preset mode.
 * - If segmented: number of segments + optional base frame
 * - If continuous: free number of states (10101), defaults to 16
 */
export function effectiveExportStates(preset: Preset): number {
  const segmented = !!preset.main?.segmented;

  console.log("[effectiveExportStates]", {
    segmented,
    segments: preset.main?.segments,
    states: preset.states,
  });

  if (segmented) {
    const segs = preset.main?.segments ?? 0;
    const includeBase = preset.base?.enabled ? 1 : 0;
    return Math.max(1, segs + includeBase);
  }

  //  Continuous mode: free states (default 16, clamp 10101)
  const freeStates = clamp(preset.states ?? 16, 10, 101);
  console.log("[effectiveExportStates] Continuous mode states:", freeStates);
  return freeStates;
}

// Reuse offscreen canvas for performance
const offscreen =
  typeof document !== "undefined"
    ? document.createElement("canvas")
    : ({} as HTMLCanvasElement);

/**
 * Renders a single PNG state frame as a Blob.
 * @param valueIndex Index of this state
 * @param total Total number of states to render
 */
export async function renderStateBlob(
  preset: Preset,
  valueIndex: number,
  total: number
): Promise<Blob | null> {
  const ctx = offscreen.getContext("2d")!;
  const { width, height } = preset.canvas;
  buildCanvas(ctx, width, height);
  ctx.clearRect(0, 0, width, height);

  // Optional background
  if (preset.canvas.background !== "transparent") {
    ctx.fillStyle = preset.canvas.background;
    ctx.fillRect(0, 0, width, height);
  }

  const segmented = !!preset.main?.segmented;

  // Calculate normalized 0..1 progress
  const v01 = segmented
    ? valueIndex / Math.max(1, preset.main?.segments || 1)
    : total === 1
    ? 1
    : valueIndex / (total - 1);

  if (preset.mode === "arc") drawArcGauge(ctx, preset, v01);
  else drawBarGauge(ctx, preset, v01);

  return await new Promise<Blob | null>((res) =>
    offscreen.toBlob((b: Blob) => res(b), "image/png")
  );
}

/**
 * Builds and returns a ZIP containing all PNG state frames.
 */
export async function exportZip(preset: Preset, namePrefix?: string) {
  console.log("[EXPORT DEBUG]", {
    segmented: preset.main?.segmented,
    segments: preset.main?.segments,
    states: preset.states,
  });

  const zip = new JSZip();
  const count = effectiveExportStates(preset);
  const zpad = count >= 100 ? 3 : 2;

  for (let i = 0; i < count; i++) {
    const blob = await renderStateBlob(preset, i, count);
    if (blob) {
      zip.file(
        `${namePrefix || preset.namePrefix || "gauge"}_${pad(i, zpad)}.png`,
        blob
      );
    }
  }

  return await zip.generateAsync({ type: "blob" });
}

/**
 * Direct PNG download without ZIP (for debugging or dev usage).
 */
export async function exportStatesPNG(preset: Preset, states = 101) {
  const canvas = document.createElement("canvas");
  const ctx = canvas.getContext("2d")!;
  buildCanvas(ctx, preset.canvas.width, preset.canvas.height);

  const total = preset.main?.segmented
    ? Math.max(1, preset.main?.segments || 1)
    : clamp(states, 10, 101); //  also clamped to 10101 here

  for (let i = 0; i < total; i++) {
    const progress = preset.main?.segmented
      ? i / Math.max(1, preset.main?.segments || 1)
      : i / (total - 1);

    ctx.clearRect(0, 0, canvas.width, canvas.height);

    if (preset.mode === "arc") {
      drawArcGauge(ctx, preset, progress);
    } else {
      drawBarGauge(ctx, preset, progress);
    }

    const blob = await new Promise<Blob | null>((resolve) =>
      canvas.toBlob(resolve, "image/png")
    );
    if (!blob) continue;

    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${preset.namePrefix || "gauge"}_${pad(i, 3)}.png`;
    a.click();
    URL.revokeObjectURL(url);
  }
}
